"use server";

import { AuthFormSchema, type FormState } from "@/validations/auth";
import z from "zod";

export async function registerUserAction(
  prevState: FormState,
  formData: FormData
): Promise<FormState> {
  const fields = {
    email: formData.get("email") as string,
    password: formData.get("password") as string,
  };
  const validatedFields = AuthFormSchema.safeParse(fields);
  if (!validatedFields.success) {
    const flattenedErrors = z.flattenError(validatedFields.error);
    console.log(flattenedErrors.fieldErrors);
    return {
      success: false,
      errors: flattenedErrors,
      data: fields,
    };
  }

  const response=await regist(validatedFields.data);
  return { success: true, data: validatedFields.data };
}
